.blocked-body {
  overflow: hidden;
}
.menu-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  padding: 30px ;
  transform: translateX(100%);
  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.menu-container.is-open {
  transform: translateX(0);
}
.mobile-btn {
  position: absolute;
  top: 10px;
  right: 15px;
  width: 40px;
  height: 40px;
}
.mob-logo__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-bottom: 40px;
  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background-color: #e8eaef;
    position: absolute;
    bottom: -20px;
    right: 0;
  }
}

.mob-nav__item {
  text-align: center;
  position: relative;
  &:not(:last-child) {
    margin-bottom: 30px;
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 5px;
      height: 5px;
      background-color: var(--primary-text-colror);
      border-radius: 50%;
      bottom: -20px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
.mob-nav__link {
  font-size: 26px;
  line-height: 1.36;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--primary-text-colror);
}
.mob-social__list {
  display: flex;
  margin-bottom: 30px;
  .mob-social__item:not(:last-child) {
    margin-right: 50px;
  }
}
.mob-lang__list {
  display: flex;
  justify-content: end;
  .mob-lang__btn {
    position: relative;
    background-color: inherit;
    border: none;
    font-size: 18px;
    line-height: 1.36;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #9999a0;
  }
}
